/****************************************************************************
  Body
 ***************************************************************************/
body {
    position: relative;
    height:100%;
    overflow-x: hidden;
    min-height: 100%;
    font-weight: $regular-weight;
    text-transform: none;
    font-family: $font-primary;
    @extend %fontsize16;
    color:$gray-600;
    background-color: $body-bg;
}
img{
    max-width: 100%;
    height: auto;
    -webkit-flex-shrink: 0;
    -moz-flex-shrink: 0;
    -ms-flex-shrink: 0;
    flex-shrink: 0;
}
a{
    @include transition(all,0.2s,ease,0s);
    &:hover{
        text-decoration: none;
    }
}

/****************************************************************************
  Headings
 ***************************************************************************/
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
    font-weight: $bold-weight;
    color:$gray-700;    
}
h1{
    @extend %fontsize50;
}
h2{
    @extend %fontsize36;   
}
h3{
    @extend %fontsize24;
}
h4{
    @extend %fontsize20;
}
h5{
    @extend %fontsize18;
}
h6{
    @extend %fontsize16;
}

