//****************************************************************************
//  General extensions
//***************************************************************************/

%inherit_textstyles {
    color: inherit;
    font-size: inherit;
    font-weight: inherit;
    letter-spacing: inherit;
    line-height: inherit;
    text-transform: inherit;
    text-align: inherit;
    font-style: inherit;
}

//****************************************************************************
//  Font sizes extensions
//***************************************************************************/
%fontsize50 {
    font-size: 50px;
    line-height: 55px;
}
%fontsize36 {
    font-size: 36px;
    line-height: 44px;
}
%fontsize30 {
    font-size: 30px;
    line-height: 38px;
}
%fontsize24 {
    font-size: 24px;
    line-height: 34px;
}
%fontsize20 {
    font-size: 20px;
    line-height: 28px;
}
%fontsize18 {
    font-size: 18px;
    line-height: 26px;
}
%fontsize16 {
    font-size: 16px;
    line-height: 24px;
}
%fontsize14 {
    font-size: 14px;
    line-height: 18px;
}
@include media-breakpoint-only(md){
    %fontsize50 {
        font-size: 36px;
        line-height: 44px;
    }
    %fontsize36 {
        font-size: 28px;
        line-height: 34px;
    }
    %fontsize24 {
        font-size: 24px;
        line-height: 34px;
    }
    %fontsize20 {
        font-size: 20px;
        line-height: 28px;
    }
    %fontsize18 {
        font-size: 18px;
        line-height: 26px;
    }
    %fontsize16 {
        font-size: 14px;
        line-height: 20px;
    }
    %fontsize14 {
        font-size: 14px;
        line-height: 18px;
    }

}
@include media-breakpoint-down(sm){
    %fontsize50 {
        font-size: 28px;
        line-height: 36px;
    }
    %fontsize36 {
        font-size: 24px;
        line-height: 34px;
    }
    %fontsize24 {
        font-size: 18px;
        line-height: 24px;
    }
    %fontsize20 {
        font-size: 18px;
        line-height: 24px;
    }
    %fontsize18 {
        font-size: 16px;
        line-height: 24px;
    }
    %fontsize16 {
        font-size: 14px;
        line-height: 22px;
    }
    %fontsize14 {
        font-size: 12px;
        line-height: 16px;
    }
}
