//****************************************************************************
//  Bootsrap variables overrides
//***************************************************************************/
$font-primary:                'Lato';
$font-family-sans-serif:      $font-primary;
$btn-box-shadow:              none;
$container-max-widths: (
sm: 540px,
md: 768px,
lg: 960px,
xl: 1200px
); 
$grid-breakpoints: (
xs: 0,
sm: 576px,
md: 768px,
lg: 992px,
xl: 1400px
);

$body-bg:      #ecf1f5;
$primary-color:#26a69a;
$primary: $primary-color;
$brand-color: #007bff;
$white:#fff;
$black:#000;
//****************************************************************************
//  COLOR 
//***************************************************************************/
$brand-link-color:            $brand-color;           
$brand-link-hover-color:      $brand-color;
$cta-link-bg:                 rgba(224,215,150,0.7);
$gray-link-bg:                #e0e0e0;
$red:                         #dc3545;
$light-red:                   rgba(220, 53, 69,0.5);
$light-primary:               rgba(38, 166, 154,0.5);

//****************************************************************************
//  Background
//***************************************************************************/


//****************************************************************************
//  Font Weight
//***************************************************************************/
$light-weight:                300;
$regular-weight:              400;
$medium-weight:               500;
$semibold-weight:             600;
$bold-weight:                 700;

//****************************************************************************
//  Buttons 
//***************************************************************************/

//Normal Button
$btn-primary-bg:              $brand-color;
$btn-primary-hover-bg:        $white;
$btn-primary-hover-color:     $brand-color;
$btn-primary-color:           $white;


//****************************************************************************
//  INPUT
//***************************************************************************/
$input-bg-color:             #f5f5f5;
$input-color:                #777;