/****************************************************************************
 *General
 ***************************************************************************/

main{
    position: relative;
    top: 69px;  
    width: 100%;
    @include inline-block;
}
.main-content{
    position: relative; 
    padding: 0 0 90px;
    margin-left: 260px;
}
.chart-wrapper{
    padding:20px 0;
}
.bg-success{
    background-color: $primary-color !important;
}
.bg-brand{
    background-color: $brand-color;
    color: $white;
}

@include media-breakpoint-down(lg){
    .main-content{
        margin-left: 0;
        left: 0;
        @include transition(all,0.3s,ease,0s);
    }
}