/****************************************************************************
 * Sidebar Sass
 ***************************************************************************/
.sidebar{
    position: fixed;
    width: 260px;
    height: 100%;
    top: 69px;
    .nav-tabs{
        border-bottom: 0;
        background-color: $gray-700;
        .nav-link{
            border: 0;
            border-radius: 0;
            padding: 10px 18px;
            color: $white;
            &:hover,
                &.active{
                color: $gray-700;
                background-color: $white;
            }
        }
    }
    .tab-content{
        padding: 10px 0;
    }
}
.profile-content{
    margin: 10px auto;
    text-align: center;
    img{
        margin-bottom: 8px;
        width: 70px;
    }
    h5{
        color: $white;
        font-weight: $regular-weight;
        @extend %fontsize14;
    }
}
.menu-heading{
    padding: 10px 15px;
    h3{
        color: $white;
        @extend %fontsize16;
        text-transform: uppercase;
        margin-bottom: 0;
    }
}
.sidebar-nav{   
    max-height: 600px;
    li{
        width: 100%;
        @include inline-block;
        a{
            padding: 7px 30px 7px 15px;
            border-left: solid 3px transparent;
            text-transform: capitalize; 
            width: 100%;
            position: relative;
            @include inline-block;
            color: $white;           
            @extend %fontsize16; 
            &:hover,
            &.active{    
                background-color: rgba(0,0,0,0.8);
                border-left: solid 3px $primary-color;          
                color: $primary-color;
            }
            .fa{
                width: 25px;
                &.fa-angle-down{
                    width: auto;
                    position: absolute;
                    right:15px;
                    font-size: 20px;
                    top:9px;
                }
            }
        }

        ul{
            display: none;
            padding-left: 15px;
        }
        &.sub-open{
            .fa-angle-down{
                @include rotate(180deg);
            }
        }
    }
}

/** * Scrollbar Styling * **/
.mCSB_scrollTools .mCSB_draggerRail{
    width: 4px;
}
.mCSB_inside > .mCSB_container{
    margin-right: 0;
}
.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar,
.mCSB_scrollTools .mCSB_dragger:hover .mCSB_dragger_bar{
    background-color: $primary-color !important;
}

/*** * Comment Styling * ****/

.comments-wrap{
    padding:0;
    li{
        list-style: none;
        border-top: solid 1px rgba(255,255,255,0.3);
        width: 100%;
        @include inline-block;
        a{
            position: relative;
            padding: 8px 15px;
            @include inline-block;
            width: 100%;
            color: $white;
            @extend %fontsize16;
            .fa{
                font-size: 12px;
                position: absolute;
                right: 15px;
                top: 15px;
            }
        }
        &:hover{
            a{
                background-color: rgba(0,0,0,0.8);
                color: $primary-color;
            }
        }
        img{
            width: 35px;
            height: 35px;
            border: solid 1px $gray-500;
            margin-right: 10px; 
        }
    }
}

/**** * Notifivation Styling * ****/

.tab-pane{
    &.notification{
        .dropdown-wrapper{
            background-color: transparent;
            max-width: 100%;
            left: 0;
            padding-left: 0;
            color: $white;
            box-shadow: none;
            position: relative;
            &:before{
                display: none;
            }    
            h6,h5{
                color: $white;
            }
            li{
                &:hover{
                    color: $gray-700;
                    h6,h5{
                        color: $gray-700;
                    }
                }
            }
        }
    }
}

/***** * * Calender Styling * *******/
.calender-wrap{
    list-style: none;
    padding: 0;
    a{
        display: table;
        width: 100%;
        color:$white;
        border-top:solid 1px rgba(255,255,255,0.3);    
        .activity-name,.activity-timing{
            display: table-cell;
            font-size: 12px;
            line-height: 15px;
            padding:10px;
            vertical-align: middle;
        }
        .activity-timing{  
            padding-right: 0;
            width:110px;
            span{
                background-color: $primary;
                border-radius: 30px 0 0 30px;
                padding: 5px 10px;
                width:75px;
                float:right;
            }
            border-right: solid 1px rgba(255,255,255,0.3);
        }

        &:hover{ 
            background-color: rgba(0,0,0,0.8);
        }
    }
    li{
        &:last-child{
            a{
                border-bottom: solid 1px rgba(255,255,255,0.3);  

            }
        }
    }
}

@include media-breakpoint-down(lg){
    .menu--open{
        .sidebar{
            left:0;
            z-index: 777;
        }
        .main-content{         
            left:260px;           
        }
    }
    .sidebar{
        left:-260px;
        @include transition(all,0.3s,ease,0s);
    }
}