/****************************************************************************
 * Product Table  Sass
 ***************************************************************************/

.latest-update-box{
    position: relative;
    padding: 15px;
    &:after{
        content: "";
        position: absolute;
        background: $brand-color;
        height: 100%;
        width: 2px;
        top: -15px;
        left: 45px;
        z-index: 1;
    }
}
.latest-update-row{
    padding:7px 0;
    @include inline-block;   
    width:100%;   
}
.update-meta{    
    position: relative;
    z-index: 2;
    margin-left: 15px;
    @include inline-block;
    vertical-align: middle;
    .update-icon{
        width: 30px;
        height: 30px;
        border-radius: 50%;
        text-align: center;
        padding: 7px 8px;
        font-size: 16px;
        color: #fff;
        @include inline-block;       
        &.fa-suitcase{
            background-color: $danger;
            @include box-shadow(0 0 0 4px $light-red);
        }
        &.fa-check{
            background-color: $primary-color;
            @include box-shadow(0 0 0 4px $light-primary);
        }
        &.fa-circle{
            width:10px;
            height:10px;
            @include box-shadow(0 0 0 4px $light-primary);
            padding: 0;
            font-size:10px;
            margin-left:10px;
        }
    }
    img{
        &.update-icon{
            width: 35px;
            height:35px;
            padding:0;        
        }
    }
}
.update-detail{
    padding-left: 25px;
    @include inline-block;
    vertical-align: middle;
    a{
        h6{
            margin-bottom:0;
        }        
    }
    p{
        margin-bottom: 0;
    }
}
.update-container{
    height: 340px;
}
.progress-container{
    height: 340px;
    .progress{
        height: 10px;
    }
    .card-block{
        margin-bottom: 30px;
        padding: 0 20px;
        h6{
            margin-bottom: 15px;
        }
    }
}

@include media-breakpoint-down(xs) {
    .latest-update-box{
        padding: 0 15px;
        &:after{
            display: none;
        }
    }
    .update-detail{
        padding-left: 10px;
        p{
            margin-bottom: 0;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            max-width: 170px;
            a{
                position: absolute;
                right:15px;
            }
        }
    }
    .update-meta{
        margin-left: 0;
        .update-icon{
            &.fa-circle{
                width: 6px;
                height: 6px;
                font-size:  6px;
                margin-left: 0;
            }
        }
    }

}