.form-check{
    margin-bottom: 10px;
}
@media (max-width:1070px){
    .horizontal-form{
        input{
            display: block;
            width: 100%
        }
    }
    .form-row{
        .col-auto{
            width:100%;
        }
        
    }
    .form-inline{
        .form-control{
            width: 100%;
            margin-bottom: 10px;
        }
        .input-group{
            width:100%;
            .form-control{
                width: auto;
                margin-bottom: 0;
            }
        }
    }
    
}