/****************************************************************************
 * Button
 ***************************************************************************/

.list-group{
    margin-bottom: 15px;
}
.carousel-item{
    padding:10px 15px;
}
.progress{
    margin-bottom: 15px;
}
/** * Breadcrumb Style  * **/
.breadcrumb-section{
    width: 100%;
    padding:10px 20px;
    background-color: #fff;
    @include inline-block;    
    .breadcrumb{
        background: transparent;
    }
    h3{
        margin-bottom: 0;
    }
}
.alert{    
    padding:15px;
    .fa{
        margin: 0 6px;
    }
    .close + .fa{
        padding-right: 15px;
    }
    &.alert-default{
        margin-top: 30px;
    }
}
.modal-wrap{
    h6{
        min-width: 200px;
        @include inline-block;
        vertical-align: middle;
    }
}
.accordion-wrap{
    .card-header{
        border-radius: 4px;
        h4 {
            margin-bottom: 0;
            .btn{
                padding:0;
                margin-bottom: 0;
            }
        }
    }
    .card{
        margin-bottom: 0;
    }
    .panel-heading{
        border-radius: 4px;
        h4{
            margin-bottom: 0;
        }
    }
    .panel{
        box-shadow: none;
    }
}
.tabs-wrap{
    .nav-tabs{
        padding-bottom: 0;        
        li{       
            @include inline-flexbox;
            margin-bottom: -1px;
            a{
                border:solid 1px transparent;
                padding:10px 15px;
                @extend %fontsize18;
                .fa{
                    padding-right: 6px;
                }
                &.active,&:focus{
                    border: 1px solid $primary-color;
                    border-bottom-color: rgb(231, 231, 231);
                    border-bottom-color: #fff;
                    background: inherit;
                    color: $gray-800;
                }
            }

        }
    }
}
.table{
    td{
        vertical-align: middle;
    }
    margin-bottom: 0;
}
.dropdown-toggle{
    &:after{
        position: relative;
        top:3px;
    }
}

@include media-breakpoint-down(sm){
    .breadcrumb-section{
        padding: 15px 15px 0;
    }
    .alert{
        button{
            &,close{
                position: relative;
                top: -10px;
                right: -5px;
            }
        }
    }
}
@include media-breakpoint-only(xs){
    .tabs-container{
        overflow-y: scroll;
        .tabs-wrap{
            width:480px;
        }
    }
}