/****************************************************************************
 * Product Table  Sass
 ***************************************************************************/

.card{
    margin-bottom: 15px;
    &.card-default{
        .card-header{
            background-color: transparent;
            border-bottom: none;
            padding: 20px 20px 15px;
            position: relative;
            h5{
                position: relative;
                margin-bottom: 0;
                @include inline-block;
                &:after{
                    content: "";
                    width: 75%;
                    height: 1px;
                    background-color: $gray-700; 
                    display: block;
                    margin-top:  5px;
                }
            }
        }
    }
}
.table{
    th,td{
        padding: 10px 20px;
    }
}
.card-header-right{
    right: 10px;
    top: 13px;
    display: inline-block;
    float: right;
    padding: 7px 0;
    position: absolute;
    .card-option{
        width: 30px;
        overflow:hidden;
        height:25px;
        &.open{
            li{
                .fa-angle-left{
                    &:before{
                        content: "\f00d";
                    }
                }
            }
        }
        li{
            @include inline-block;
            .fa{
                margin: 0 4px;
                cursor: pointer; 
                color: $gray-500;
                font-weight: $light-weight;
                @extend %fontsize20;
            }
        }
    }
}

.table-hover {
    tbody{
        tr{
            &:hover{
                background-color: rgba(38,166,154,0.3);
            }
        }
    }
    label{
        border-radius: 4px;
        font-size: 75%;
        padding: 0 7px;
        margin-right: 5px;
        font-weight: 400;
        color: #fff;
    }
}
.rating-status{
    a{
        &.gray-color{
            color: $gray-300;
        }
    }
}
.latest-updated-section{
    .card{
        .card-header{
            padding-right: 140px;
        }
    }
}
@include media-breakpoint-down(md){
    .card{
        margin-bottom: 30px;
    }
}