/****************************************************************************
 * Page Header
 ***************************************************************************/
.logo{
    padding: 10px 0;
    @include inline-block;
}
.header{
    width:100%;
    @include inline-block;
    .navbar{
        padding:0 15px;
    }
}
/**** * Notification Stying * ***/
.notification{   
    .dropdown-toggle{
        position:relative;
        &:after{
            display: none;
        }
    }
    .badge{
        border-radius: 50%;
        width: 18px;
        height: 18px;
        position: absolute;
        top: -6px;
        right: -15px;
        font-size: 12px;
        line-height: 13px;
    }    
    ul{
        li{
            label{
                cursor: pointer; 
            }
            &:first-child{
                &:hover{
                    background-color: transparent;
                }
            }
        }
    }
}
.navbar-right{
    @include inline-block;
    ul{
        list-style: none;
        margin-bottom:0;
        padding-left:0;
        &.top-right-menu{
            & > li{
                padding: 15px 20px;
                height: 100%;
                @include inline-block;
                border-right:solid 1px rgba(255,255,255,0.1);
                a{
                    color: $white;
                }
                &.notification{
                    padding: 22px 30px 22px 20px;
                }
                &:last-child{
                    padding-right: 0;
                    border-right: 0;
                }
                .fullscreen-btn{
                    padding:7px 0;
                }
            }
        }
    }
    .dropdown-toggle{
        &:after{
            display: none;

        }
    }
}

.dropdown-wrapper{
    max-width: 450px;
    position: absolute;
    left: -300px;
    border: 0;
    @include box-shadow(0 0 20px 0 rgba(0,0,0,.4))

    &:before{
        content: "\f0d8";
        font-family: "FontAwesome";
        position: absolute;
        top: 0;
        right: 20px;
        font-size: 32px;
        color: #fff;
        top: -17px;
    }
    li{
        padding:10px 15px;
        width: 100%;
        @include inline-block;
        &:hover{
            background-color: $gray-200;
        }

    }
    .media{
        img{
            width:45px;
            margin-right: 10px;
        }
        .notification-time{
            font-size: 12px;
            line-height: 12px;
        }
        .notification-user,
        .notification-msg{
            margin-bottom: 5px;
            @extend %fontsize14;
        }
    }
    h6{
        @include inline-block;
    }
    .label{
        color: $white;
        float:right;
        padding: 0 10px;
        border-radius: 5px;
    }
}

/** *Profile Styling * **/

.profile-wrapper{
    .rounded-circle{
        width: 37px;
        margin-right: 10px;
    }
    .dropdown-toggle{
        cursor: pointer;
        color: $white;
        .fa{
            padding-left:10px;
            font-size: 16px; 
        }
    }
    .dropdown-wrapper{
        width: 240px;
        left: -100px;
        li{
            padding:5px 15px;
            width: 100%;
            @include inline-block;
            .fa{
                width: 25px;
            }
        }
    }
}

.fullscreen-btn{  
    @include inline-block;
    &.fullscreen-active{
        .fa-expand{
            &:before{
                content: "\f066";
            }
        }
    }
}
.navbar-expand-lg {
    .navbar-nav {
        .nav-link{
            padding:10px 15px;
        }
    }
}
.navbar-toggler{
    float: right;
    border: 0;
    padding:0;
}
.navbar-brand{       
    @extend %fontsize30;
    color: $white;
    margin-left:-3px;
    span{
        color: $primary;
    }
    text-transform: uppercase;
    &:hover{
        span{
            color: $white;
        }
        color: $primary !important;
    }
}
.menu-trigger{
    display: none;
}


@include media-breakpoint-down(lg){
    .navbar{
        padding: 10px 15px;
        .container{
            max-width: 100%;
        }
        .nav-link{
            font-size: 16px;
            line-height: 24px;
        }
    }
    .navbar-expand-lg .navbar-nav .nav-link{
        padding: 10px 0;
    }
    .menu-trigger {
        position: absolute;
        left: 15px;
        top: 25px;
        cursor: pointer;
        height: 21px;
        width: 28px;
        padding: 0;
        display: block;
        @include transition(all, 0.2s, ease, 0s);
        .bars {
            background-color: $primary-color;
            border-radius: 0;
            height: 3px;
            margin-bottom: 6px;
            width: 100%;
            @include transition(all, 0.2s, ease, 0s);
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    .menu--open {
        main{
            overflow-x: hidden;           
        }
        overflow-x: hidden !important;
        .bars {
            &:first-child {
                -webkit-transform: translateY(10px) rotate(136deg);
                transform: translateY(10px) rotate(136deg);
            }
            &:last-child {
                -webkit-transform: translateY(-8px) rotate(-136deg);
                transform: translateY(-8px) rotate(-136deg);
            }
            &:nth-child(2) {
                -webkit-transform: scale(0);
                transform: scale(0);
            }
        }   
    }
    .navbar{
        text-align: center;
        display: block;
        .nav-link{
            font-size: 14px;
            line-height: 24px;
            padding:5px 0;
        }
    }
    .navbar-left{
        padding-left:50px;
        float: left;
        @include inline-block;
    }
    .navbar-right{
        float:right;
        ul{
            &.top-right-menu{
                & >li{
                    display: none;
                    &.profile-wrapper{
                        display: block;
                    }
                }
            }
        }
    }

}
@include media-breakpoint-down(sm){
    .profile-wrapper {
        .dropdown-wrapper{
            width: 200px;
            left: -75px;
        }
    }
}
@include media-breakpoint-only(xs){
    .logo{
        .navbar-brand{
            font-size: 20px;
            margin-right:0;
        }
    }
    .navbar-right{
        ul{
            &.top-right-menu {
                .profile-wrapper{
                    padding-left:5px;
                    .rounded-circle{
                        width:32px;
                        margin-right: 6px;
                    }
                }
            }
        }
    }
}