/****************************************************************************
 * Main Footer
 ***************************************************************************/
.footer{
    position: absolute;
    bottom: 0;
    left:0;
    padding:0 15px;
    width: 100%;
    margin-top:30px;
}
.footer-wrap{
    a{
        .fa{
            @extend %fontsize30;
            padding-left: 10px;
        }
    }
}
@include media-breakpoint-only(xs) {
    .footer{
        margin-top: 10px;
    }
    .footer-wrap{
        padding:20px 0;
        text-align: center;
        .pull-right{
            float: none;
            display: block;
          margin-top:10px;;
        }
    }
}