/****************************************************************************
 * 404 Style
 ***************************************************************************/
.error-page-section{
    display: block;
    width: 100%;   
    position: absolute;
    height: 80%;
    .error-content{    
        h1{
            color: $danger;
            font-size:  170px;
            line-height: 200px;
        }
        h2{
            font-size: 40px;
            line-height: 50px;
            color: $danger;
        }
    }
}
@include media-breakpoint-up(md){
    .error-page-section{
        .error-content{
            max-width: 700px;
            margin-top:  -40px;
            display: block;
            @include horizontal-vertical-align(absolute);
        }
    }
}
@include media-breakpoint-down(sm){
    .error-page-section{
        position: relative;
        height: auto;
        .error-content{
            padding: 30px 15px;
            display: block;
            position: relative;
            text-align: center;
            h1{              
                font-size:  100px;
                line-height: 130px;
            }
            h2{
                font-size: 30px;
                line-height: 40px;
            }
        }
    }
}