/****************************************************************************
 * Button
 ***************************************************************************/

.btn{
    text-transform: capitalize;
    box-shadow: none!important;
    margin-bottom: 10px;
    padding: 5px 15px 7px;
    &.btn-rounded{
        border-radius: 30px;
    }
    .fa{
        margin: 0  6px;
    }
    &.btn-xs{
        padding:0 15px 2px;
    }
    &.btn-circle{
        border-radius: 50%;
        width:50px;
        height: 50px;     
        .fa{
            margin: 0;
        }
    }
}
.btn-group-justified{
    display: table;
    width: 100%;
    table-layout: fixed;
    border-collapse: separate;
    .btn-group{
        display: table-cell;
        .btn{
            width:100%;
        }
    }
}
.btn-group{
    .dropdown-menu{
        padding:10px;
    }
}


@include media-breakpoint-only(xs){
    .btn-group-justified{
        display: block;
        width: 100%;
        .btn-group{
            display:block;
            margin-bottom: 15px;
            .btn{
                width:auto;
                min-width:150px;
            }
        }
    }
}