/****************************************************************************
 * Login Style
 ***************************************************************************/
::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: $gray-100;
}
::-moz-placeholder { /* Firefox 19+ */
  color:  $gray-100;
}
:-ms-input-placeholder { /* IE 10+ */
  color:  $gray-100;
}
:-moz-placeholder { /* Firefox 18- */
  color:  $gray-100;
}
.login-section{
    position: relative;
    display: block;
    padding:150px 0;
}
.login-wrap{
    width:100%;
    max-width: 600px;
    margin: 0 auto;  
    border-radius: 5px;
    background-color:  $white;
    padding: 30px 25px 20px;
    h3{
        margin-bottom: 20px;
        text-align: center;
    }
    .btn{
        min-width: 120px;
        @extend %fontsize18;
        padding:5px 15px 7px;
        float: right;
        margin-top:10px;
    }
}
@include media-breakpoint-down(sm){
    .login-section{
        padding: 40px 0 20px;
    }
    .login-wrap{
        padding:20px 15px  15px;
        h3{
            margin-bottom: 10px;
        }
    }
}
@include media-breakpoint-only(xs){
    .login-wrap{
        max-width:90%;
    }
}