/****************************************************************************
 * Search
 ***************************************************************************/

.main-search{
    padding: 6px 0;
    @include inline-block;
    input{
        &.form-control{
            display: none;
            background-color: transparent !important;
            border: 0 !important;
            outline: 0 !important;
            padding: 0 10px;
            color: $white !important;
            box-shadow: none !important;
            @include transition(all,0.2s,ease,0s);
        }
    }
    .ion{
        color: $white;
        font-size: 20px;
        padding-top: 5px;
        &.ion-search{
            padding-right:10px;
        }
        &.ion-close{
            font-size: 16px;
            padding-top: 7px;
        }
    }
    .search-close{
        display: none;
        cursor: pointer;
    }
    .search-btn{
        cursor: pointer;
        height: 27px;
    }
    input::-webkit-input-placeholder { 
        color: $white;
    }
    input::-moz-placeholder { 
        color: $white;
    }
    input:-ms-input-placeholder {
        color: $white;
    }
    input:-moz-placeholder { 
        color: $white;
    }
}
