/****************************************************************************
 * Sidebar Sass
 ***************************************************************************/
.panel{
    margin-bottom: 20px;
    background-color: #fff;
    border: none;
    box-shadow: 0 1px 1px rgba(0,0,0,.05);
    @include border-radius(5px);
    .icon-col {
        text-align: center;
        padding: 25px 0;
        color: $white;        
    }
    .text-col{
        padding: 10px 20px;
        .fa{
            position: absolute;
            right: -20px;
            bottom: -40px;
            color: rgba(0,0,0,.05);
            font-size: 120px;
        }
    }
    &.panel-primary{
        & > .panel-heading{
            background-color: $primary-color;
        }       
    }
    .panel-title {
        color: $white;
    }
    .panel-heading{
        border-radius: 4px 4px 0 0;
        padding:15px 20px 10px;      
        color: $white;
        h4{
            color:$white;
            a{
                color: $white;
            }
        }
        &:hover{
            h4{
                a{
                    color: $gray-700
                }
            }
        }
    }
    .panel-body{
        border-radius: 0 0 4px 4px;
        padding: 20px 20px 10px;
        position: relative;
        width: 100%;
        @include inline-block;

    }
}
.striped-bg {
    background-image: url(../images/bg-strips-light.png);
    background-repeat: repeat;
    overflow: hidden;
}
@include media-breakpoint-up(md){
    .panel{
        .icon-col{
            .fa{
                margin-left: 8px;
                @include horizontal-vertical-align(absolute);
            }
        }
    }
}

@include media-breakpoint-down(lg){
    .panel{
        .icon-col{
            padding:15px;
            .fa{
                font-size: 40px;
            }
        }
    }

}
@include media-breakpoint-down(md){
    .panel{
        .panel-body{
            padding:20px 10px 10px;
            img{
                &.pull-left,
                &.pull-right{
                    display: block;
                    float:none;
                    margin-bottom: 15px;
                }
            }
        }
        .panel-btn{
            margin-top:15px;
        }
    }
}
@include media-breakpoint-down(sm){
    .panel{
        .text-col{
            text-align: center;
        }
    }
}